import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    deviceMark: '',
  },
  getters: {
    getDeviceMark(state) {
      return state.deviceMark
    }
  },
  mutations: {
    setDeviceMark(state, payload) {
      state.deviceMark = payload
    }
  },
  actions: {
  },
  modules: {
  }
})
