<template>
  <div @click="btnAction" class="btn" :class="style">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "Button",
  props: {
    mode: {
      type: String,
      default: 'outline',
    },
    disabled: {
      type: Boolean,
    }
  },
  mounted() {

  },
  methods: {
    btnAction() {
      if (!this.disabled) this.$emit('click')
    }
  },
  data() {
    return {
      text: 'test',
    }
  },
  computed: {
    style() {
      if (this.mode == 'primary') return 'btn-primary';
      if (this.mode == 'outline') return 'btn-outline';
      if (this.mode == 'outline-gray') return 'btn-outline-gray';
      if (this.mode == 'none') return 'btn-none';
      return 'btn-outline'
    }
  },
}
</script>

<!--TODO: Global Variable 2-->
<style lang="scss" scoped>

.btn {
  cursor: pointer;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.25rem;
  user-select: none;

  &-primary {
    color: white;
    background-color: #666666;
    font-weight: 700;
    height: 46px;

    &:hover {
      background-color: #4C4C4C;
    }
  }

  &-outline {
    color: #333333;
    background-color: transparent;
    border: 2px solid #666666;
    font-weight: 700;
    user-select: none;
    height: 46px;

    &:hover {
      background-color: #F5F5F5;
    }
  }

  &-outline-gray {
    color: #666666;
    background-color: transparent;
    border: 1.5px solid #666666;
    font-weight: 700;
    user-select: none;

    &:hover {
      background-color: #F5F5F5;
    }
  }

  &-none {
    color: #666666;
    background-color: transparent;
    font-weight: 700;
    user-select: none;
  }

  &Three {
    cursor: default;
    color: #4C4C4C;
  }
}

</style>