import Vue from 'vue'
import VueRouter from 'vue-router'
const Welcome = () => import('@/pages/Welcome')
const DeviceMark = () => import('@/pages/DeviceMark')
const DeviceModel = () => import('@/pages/DeviceModel')
const PackageIssue = () => import('@/pages/PackageIssue')
const WarmAndColdWater = () => import('@/pages/WarmAndColdWater')
const WarmAndColdWaterOrder = () => import('@/pages/WarmAndColdWater/WarmAndColdWaterOrder')
const PackageNoStart = () => import('@/pages/PackageNoStart')
const PackageNoStartStep2 = () => import('@/pages/PackageNoStart/PackageNoStartStep2')
const PackageNoStartStep3 = () => import('@/pages/PackageNoStart/PackageNoStartStep3')
